#nodemap_wrapper{
    width: 70%;
    display: flex;
    
    align-items: center;
}
#nodemap_wrapper h1{
    font-size: 20px;
}
#nodemap{
    display: flex;
    align-self: center;
}
#nodemap h1{
    font-size: 16px;
    margin-left: 10px;
}
#nodemap h1:nth-child(1){
    background: gold;
}
.node_down{
    background: red;
}